import stars from '../../components/stars.vue'
import {swiper,hot,notice,like} from '../../api/http.js'
export default {
	data() {
		return {
			title:"hello",
			swiperItem:[],
			hotItem:[],
			noticeItem:[],
			likeItem:[],
			animationData:{},
			animationDataArr:[],
			danmuList: [{
					text: '真精彩',
					color: '#ff0000',
					time: 1
				},
				{
					text: '233333',
					color: '#ffcccc',
					time: 2
				},
				{
					text: '66666',
					color: '#ff00ff',
					time: 3
				}
			],				
		}
	},
	components: {
		stars
	},
	onLoad() {

		// 在页面创建的时候，创建一个临时动画对象
		this.animation = uni.createAnimation();

		swiper().then(res=>{
			// console.log(res)
			this.swiperItem = res.data
		})
		
		hot().then(res=>{
			// console.log(res)
			this.hotItem = res.data
		})
		
		notice().then(res=>{
			// console.log(res)
			this.noticeItem = res.data
		})
		
		this.getLikeData()
	},
	onPullDownRefresh() {
		this.getLikeData()
	},
	methods: {
		gotest(){
			uni.navigateTo({
				url:"../test/test"
			})
		},
		
		getLikeData(){
			uni.showLoading({
				mask:true
			})
			uni.showNavigationBarLoading()
			like().then(res=>{
				if(res.status == 200){
					this.likeItem = res.data
				}
			})
		},
		
		// 播放一个视频的时候，需要暂停其他正在播放的视频
		playmove(e){
			var	trailerId = e.currentTarget.id;
			this.videoContext = uni.createVideoContext(trailerId)
			for(var i=0;i<this.noticeItem.length;i++){
				if(this.noticeItem[i].id != trailerId){
					uni.createVideoContext(this.noticeItem[i].id).pause()
				}
			}
		},
		
		dianzan(index){

			this.animation.translateY(-60).opacity(1).step({
				duration: 400
			});
			this.animationData = this.animation;
			this.animationDataArr[index] = this.animationData.export();
			
			setTimeout(()=>{
				this.animation.translateY(0).opacity(0).step({
					duration: 0
				});
				this.animationData = this.animation;
				this.animationDataArr[index] = this.animationData.export();
			},500)					

		}
		
	}
}
